<!-- src/views/Books.vue -->
<template>
  <div class="books content-container">
    <h1 class="books-title">My Books</h1>
    <ul class="book-list">
      <li v-for="book in books" :key="book.isbn" class="book-item">
        <div class="book-summary" @click="toggleDetails(book)">
          <div class="book-title">{{ book.title }}</div>
          <div class="book-author">{{ book.author }}</div>
          <div class="progress-container">
            <div class="progress-bar">
              <div class="progress" :style="{ width: `${book.progress}%` }"></div>
            </div>
            <span class="progress-text">{{ book.progress }}%</span>
          </div>
          <div v-if="book.progress === 100" class="completion-date">
            Completed on: {{ formatDate(book.dateRead) }}
          </div>
        </div>
        <div v-if="book.showDetails" class="book-details">
          <p><strong>ISBN:</strong> {{ book.isbn }}</p>
          <p><strong>Added on:</strong> {{ formatDate(book.dateAdded) }}</p>
          <p><strong>Publisher:</strong> {{ book.publisher }} ({{ book.yearPublished }})</p>
          <p><strong>Rating:</strong> {{ book.rating.toFixed(1) }} / 5</p>
          <p><strong>Pages:</strong> {{ book.numPages }}</p>
          <p v-if="book.shelf"><strong>Shelf:</strong> {{ book.shelf }}</p>
          <img v-if="book.image" :src="book.image" :alt="book.title + ' cover'" class="book-cover">
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { useStore } from 'vuex'

export default {
  name: 'Books',
  setup() {
    const store = useStore()
    const books = ref([])

    onMounted(() => {
      store.dispatch('content/loadBooks')
      books.value = store.getters['content/getBooks'].map(book => ({
        ...book,
        showDetails: false
      }))
    })

    const formatDate = (dateString) => {
      if (!dateString) return 'Not available'
      const options = { year: 'numeric', month: 'long', day: 'numeric' }
      return new Date(dateString).toLocaleDateString(undefined, options)
    }

    const toggleDetails = (book) => {
      book.showDetails = !book.showDetails
    }

    return {
      books,
      formatDate,
      toggleDetails
    }
  }
}
</script>

